	module.exports = {"data":{"markdownRemark":{"html":"<h3>Revamped Directory</h3>\n<p>We've totally revamped our directory page so that you can better search and view programs. You can now filter your search results by program features and asset type, and we also enable you to view various stats for each program on one page. </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/nov_2018_directory-aac165bb2dbb19415a7cc55d2896fe72-cd90c.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 51.84411969380655%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"new directory page\"\n        title=\"\"\n        src=\"/static/nov_2018_directory-aac165bb2dbb19415a7cc55d2896fe72-e7ffa.png\"\n        srcset=\"/static/nov_2018_directory-aac165bb2dbb19415a7cc55d2896fe72-49813.png 125w,\n/static/nov_2018_directory-aac165bb2dbb19415a7cc55d2896fe72-dc8a5.png 250w,\n/static/nov_2018_directory-aac165bb2dbb19415a7cc55d2896fe72-e7ffa.png 500w,\n/static/nov_2018_directory-aac165bb2dbb19415a7cc55d2896fe72-dda24.png 750w,\n/static/nov_2018_directory-aac165bb2dbb19415a7cc55d2896fe72-c5f18.png 1000w,\n/static/nov_2018_directory-aac165bb2dbb19415a7cc55d2896fe72-22484.png 1500w,\n/static/nov_2018_directory-aac165bb2dbb19415a7cc55d2896fe72-cd90c.png 2874w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Hacker Dashboard</h3>\n<p>Our new <a href=\"/hackers/hacker-dashboard.html\">Hacker Dashboard</a> enables hackers to better manage and review their accepted and pending invitations. </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/nov_2018_hacker-dashboard-d7eef4a583b7cac2b7f19f6f008e9691-ca303.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 44.989488437281004%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Hacker Dashboard\"\n        title=\"\"\n        src=\"/static/nov_2018_hacker-dashboard-d7eef4a583b7cac2b7f19f6f008e9691-e7ffa.png\"\n        srcset=\"/static/nov_2018_hacker-dashboard-d7eef4a583b7cac2b7f19f6f008e9691-49813.png 125w,\n/static/nov_2018_hacker-dashboard-d7eef4a583b7cac2b7f19f6f008e9691-dc8a5.png 250w,\n/static/nov_2018_hacker-dashboard-d7eef4a583b7cac2b7f19f6f008e9691-e7ffa.png 500w,\n/static/nov_2018_hacker-dashboard-d7eef4a583b7cac2b7f19f6f008e9691-dda24.png 750w,\n/static/nov_2018_hacker-dashboard-d7eef4a583b7cac2b7f19f6f008e9691-c5f18.png 1000w,\n/static/nov_2018_hacker-dashboard-d7eef4a583b7cac2b7f19f6f008e9691-22484.png 1500w,\n/static/nov_2018_hacker-dashboard-d7eef4a583b7cac2b7f19f6f008e9691-ca303.png 2854w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Disclosure</h3>\n<p>We've deprecated the term \"Public Disclosure\" and now simply just call it\n<a href=\"/programs/disclosure.html\">Disclosure</a>.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/nov_2018_disclosure-9856d5dbdd997199cea9f4be51b191c7-469d3.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 31.34751773049645%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"request disclosure\"\n        title=\"\"\n        src=\"/static/nov_2018_disclosure-9856d5dbdd997199cea9f4be51b191c7-e7ffa.png\"\n        srcset=\"/static/nov_2018_disclosure-9856d5dbdd997199cea9f4be51b191c7-49813.png 125w,\n/static/nov_2018_disclosure-9856d5dbdd997199cea9f4be51b191c7-dc8a5.png 250w,\n/static/nov_2018_disclosure-9856d5dbdd997199cea9f4be51b191c7-e7ffa.png 500w,\n/static/nov_2018_disclosure-9856d5dbdd997199cea9f4be51b191c7-dda24.png 750w,\n/static/nov_2018_disclosure-9856d5dbdd997199cea9f4be51b191c7-c5f18.png 1000w,\n/static/nov_2018_disclosure-9856d5dbdd997199cea9f4be51b191c7-469d3.png 1410w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Disclosure for Private Programs (beta)</h3>\n<p>Private programs can now opt-in to enable hackers to disclose reports to other hackers within their program. Upon disclosure, contents of the report will only be visible to participants within that private program. This enables hackers to share their vulnerability findings with other hackers in the program, and can also increase awareness for other hackers as they can better see what vulnerabilities have already been found for the program. </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/nov_2018_disclosure-settings-142ec3498fff0c4b1abb9b096557b63a-01095.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 41.431451612903224%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"disclosure settings\"\n        title=\"\"\n        src=\"/static/nov_2018_disclosure-settings-142ec3498fff0c4b1abb9b096557b63a-e7ffa.png\"\n        srcset=\"/static/nov_2018_disclosure-settings-142ec3498fff0c4b1abb9b096557b63a-49813.png 125w,\n/static/nov_2018_disclosure-settings-142ec3498fff0c4b1abb9b096557b63a-dc8a5.png 250w,\n/static/nov_2018_disclosure-settings-142ec3498fff0c4b1abb9b096557b63a-e7ffa.png 500w,\n/static/nov_2018_disclosure-settings-142ec3498fff0c4b1abb9b096557b63a-dda24.png 750w,\n/static/nov_2018_disclosure-settings-142ec3498fff0c4b1abb9b096557b63a-c5f18.png 1000w,\n/static/nov_2018_disclosure-settings-142ec3498fff0c4b1abb9b096557b63a-22484.png 1500w,\n/static/nov_2018_disclosure-settings-142ec3498fff0c4b1abb9b096557b63a-01095.png 1984w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Retesting</h3>\n<p>We've globally launched our <a href=\"/programs/retesting.html\">retesting</a> feature so that all programs can now initiate retests on any of their resolved reports. Invitations for retests now expire after 24 hours, and hackers are now required to provide a short summary of how they retested the vulnerability. Hackers can also provide attachments of their findings.  </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/nov_2018_retesting-03bfd71a7cd230c5854716ae752d93cc-b1111.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 61.81818181818181%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Retesting\"\n        title=\"\"\n        src=\"/static/nov_2018_retesting-03bfd71a7cd230c5854716ae752d93cc-e7ffa.png\"\n        srcset=\"/static/nov_2018_retesting-03bfd71a7cd230c5854716ae752d93cc-49813.png 125w,\n/static/nov_2018_retesting-03bfd71a7cd230c5854716ae752d93cc-dc8a5.png 250w,\n/static/nov_2018_retesting-03bfd71a7cd230c5854716ae752d93cc-e7ffa.png 500w,\n/static/nov_2018_retesting-03bfd71a7cd230c5854716ae752d93cc-dda24.png 750w,\n/static/nov_2018_retesting-03bfd71a7cd230c5854716ae752d93cc-c5f18.png 1000w,\n/static/nov_2018_retesting-03bfd71a7cd230c5854716ae752d93cc-22484.png 1500w,\n/static/nov_2018_retesting-03bfd71a7cd230c5854716ae752d93cc-b1111.png 1870w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/changelog/2018/november","title":"November 2018"}}},"pathContext":{}}

